html, body {
	width: 100%;
	overflow-x: hidden;
}
body {
	height:100%;
	font-family: "Lato", "Serif";
	font-weight: 300;
	padding:0;
	margin:0;
	font-size:16px;
	line-height: 28px;
	color: #777; 
	background: #fff;
	overflow-x:hidden;
	position: relative;
}
h1 { font-size: 3em; line-height:1.2em; margin:0 0 .3em; }
h2 { font-size: 1.8em; line-height:1.4em; margin:0 0 .3em;  }
h3 { font-size: 1.4em; line-height:1.4em; }
h4 { font-size: 1.25em; line-height:1.4em;}
h5 { font-size: 1.1em; line-height:1.4em; }
h6 { font-size: 1em; line-height:1.2em; }

h1 a, h2 a, h3 a, h4 a, h5 a, h6 a {
	text-decoration: none
}	 
p { 
	padding: 0 0 1em;
	margin:0;
}
a {
	color: #3eb0f7;
	outline: 0;
	font-weight: bold;
	-webkit-transition: all .8s ease;
	transition: all .8s ease;
	display: inline-block;
} 
a:hover {
	text-decoration: none;
	color: #1f2222;
	-webkit-transition: all .8s ease;
	transition: all .8s ease;
}
a.selected{
	color: #777;
	font-weight: normal;
	cursor: default;
}
a.user{
	font-weight: 700;
	color: #3eb0f7 !important;
}
.highlight {
	color: #3eb0f7;
}
ol{
	padding: 0 0 0 1em;
}
ol li{
	margin: 1em 0;
	text-align: justify;
}
#preloader { 
	position: fixed; 
	left: 0; 
	top: 0; 
	z-index: 999; 
	width: 100%; 
	height: 100%; 
	overflow: visible; 
	background: #fff url(../images/loading.gif) no-repeat center center;
}
.fullscreen {
    width:100%;
    min-height:100%;
	background-repeat:no-repeat;
    background-position:50% 50%;
    background-position:50% 50%\9 !important;
}
.fullscreen.landing{
	background-image:url('/images/bck-main-lg.jpg');
}
.fullscreen.contact, .fullscreen.cta{
	background-image:url('/images/bck-contact-lg.jpg');
}
.overlay {
    background-color: rgba(0, 0, 0, 0.5);
    position: relative;
    width: 100%;
    height: 100%;
    display: block;
}
#menu {
	z-index: 99;
}
.navbar-default .navbar-brand, .navbar-default .navbar-collapse {
    padding: 0 !important;
}
.navbar-default {
	background: rgba(255, 255, 255, 0.95);
	box-shadow: 0px 0.5px 6px #cecece;

	.navbar-brand, .navbar-collapse {
    	padding: 0;
    	height: auto!important;
    }
    .navbar-toggle {
    	margin: 1.5em 1em;
    }

    .dropdown-menu {
    	border: 1px solid #f5f5f5;
    	border-top: 0;
    	box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);

		li{

			a {
				line-height: 2em;
				font-weight: 300;
				padding: 0.1em 1.5em 0.5em 0.9em;
				cursor: pointer;
			}

			a:focus, a:hover{
				background-color: #f1f1f1 !important;
			}
		}
	}

	.open a, .open a:hover, .open a:focus {
		background: #fff !important;
	}
}
.navbar-default .navbar-nav > li > a {
    color: #777;
	font-weight: 500;
	line-height: 3.4em;
}
.navbar-default .navbar-nav > li > a:hover,
.navbar-default .navbar-nav > li > a:focus {
    color: #3eb0f7;
}
.navbar-default .navbar-nav > .active > a, 
.navbar-default .navbar-nav > .active > a:hover, 
.navbar-default .navbar-nav > .active > a:focus {
	background: transparent;
    color: #3eb0f7;
}
.btn-default {
	font-size: 1em;
	margin: 1em 0 0.5em;
	margin-right: 0.3em;
	line-height: 1.6em;
	padding: 0.7em 1.6em;
	border: 2px solid #3eb0f7;
	background: transparent;
	transition: all 0.4s;
	color: #3eb0f7;
	border-radius: 100px;
	display: inline-block;
}
.btn-default:hover, .btn-default:focus, .btn-default:active, .btn-default.active, .open > .dropdown-toggle.btn-default {
	border: 2px solid #3eb0f7;
	background: #3eb0f7;
	color: white;
}
.btn-primary {
	font-size: 1em;
	margin: 1em 0 0.5em;
	margin-right: 0.3em;
	line-height: 1.6em;
	padding: 0.7em 1.6em;
	border: 2px solid #3eb0f7;
	background: #3eb0f7;
	transition: all 0.4s;
	color: white;
	border-radius: 100px;
	display: inline-block;
}
.btn-primary:hover, .btn-primary:focus, .btn-primary:active, .btn-primary.active, .open > .dropdown-toggle.btn-primary {
	border: 2px solid #3eb0f7;
	background: transparent;
	color: #3eb0f7;
}
.btn-secondary {
	font-size: 20px;
	font-weight: 300;
	line-height: 20px;
	padding: 20px 50px;
	height: 65px;
	border: none;
	background: #3eb0f7;
	transition: all 0.4s;
	color: white;
	border-radius: 4px;
}
.btn-secondary:hover, .btn-secondary:focus, .btn-secondary:active, .btn-secondary.active, .open > .dropdown-toggle.btn-secondary {
	background: #1f96e0;
	color: white
}
.btn-dark {
	font-size: 1em;
	margin: 1em 0 0.5em;
	margin-right: 0.3em;
	line-height: 1.6em;
	padding: 0.7em 1.6em;
	border: 2px solid #999;
	background: #999;
	transition: all 0.4s;
	color: white;
	border-radius: 100px;
	display: inline-block;
}
.btn-dark:hover, .btn-dark:focus, .btn-dark:active, .btn-dark.active, .open > .dropdown-toggle.btn-dark {
	border: 2px solid #999;
	background: transparent;
	color: #999;
}

.site-name img {
	max-width: 300px; 
	padding-top: 0.5em;
}
.logo {
	margin: 100px 0 40px 0;
}
.landing .overlay{
	padding: 8em 0 15em 0;
}
.landing h1{
	font-size: 56px;
	font-weight: 300;
	color: #fff;
	margin: 30px 0 30px 0;
	text-shadow: 0 1px 2px rgba(0, 0, 0, .6);
}
.landing p, .landing h2 {
	color: #fff;
}
.landing-text {
	margin-bottom: 20px;
}
.landing-text p {
	font-size: 17px !important; 
}
.head-btn {
	margin-bottom: 150px;
}
.option {
